
@charset "UTF-8";
.pull-to-refresh-wrapper[data-v-f49f0618] {
  overflow: hidden;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
.box[data-v-f49f0618] {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.pull_bar[data-v-f49f0618],
.push_bar[data-v-f49f0618] {
  text-align: center;
  color: #808080;
  position: absolute;
  z-index: 10;
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  font-size: 0.8rem;
}
.pull_bar[data-v-f49f0618] {
  top: -3rem;
}
.push_bar[data-v-f49f0618] {
  bottom: -3rem;
}
.pull_bar.none[data-v-f49f0618]:before {
  content: '下拉刷新';
}
.push_bar.none[data-v-f49f0618]:before {
  content: '上拉刷新';
}
.pull_bar.release[data-v-f49f0618]:before,
.push_bar.release[data-v-f49f0618]:before {
  content: '松手刷新';
}
.pull_bar.loading[data-v-f49f0618]:before,
.push_bar.loading[data-v-f49f0618]:before {
  content: '刷新中';
}
.pull_bar.done[data-v-f49f0618]:before,
.push_bar.done[data-v-f49f0618]:before {
  content: '刷新完成';
}
.scroll[data-v-f49f0618] {
  position: absolute;
  z-index: 1;
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -o-text-size-adjust: none;
  text-size-adjust: none;
}

